import request from '@/uitls/request'

// 获取文章列表
// get请求的第二个方法是配置项,不能直接写参数
export const getArticleS = data => request.get('interview/query', {
  params: data
})

// 获取我的收藏接口
export const getArticleCollect = data => request.get('interview/opt/list', {
  params: {
    optType: 2, // 表示收藏
    ...data
  }
})

// 或取我的喜欢接口
export const getArticleLike = data => request.get('interview/opt/list', {
  params: {
    optType: 1, // 表示收藏
    ...data
  }
})

// 获取单个面经详情
export const getArticleDetail = id => request.get('interview/show', {
  params: {
    id
  }
})

// 切换点赞的接口
export const toogleLike = id => request.post('/interview/opt', {
  id: id,
  optType: 1
})
// 切换收藏接口
export const toogleStar = id => request.post('/interview/opt', {
  id: id,
  optType: 2
})
