import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login'
import Register from '@/views/register'
import Detail from '@/views/detail'
import Layout from '@/views/layout'

import Like from '@/views/like'
import Article from '@/views/article'
import Collect from '@/views/collect'
import User from '@/views/user'

import { getToken } from '@/uitls/storage'
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/login',
      component: Login
    },
    {
      path: '/register',
      component: Register
    },
    {
      path: '/detail/:id', // 表示要接受一个key为id的参数
      component: Detail
    },
    {
      path: '/',
      component: Layout,
      redirect: 'article',
      children: [
        {
          path: '/article',
          component: Article
        },
        {
          path: '/like',
          component: Like
        },
        {
          path: '/collect',
          component: Collect
        },
        {
          path: '/user',
          component: User
        }
      ]
    }
  ]
})
// 全局前置守卫：
// 1. 所有的路由一旦被匹配到，在真正渲染解析之前，都会先经过全局前置守卫
// 2. 只有全局前置守卫放行，才能看到真正的页面

// 任何路由，被解析访问前，都会先执行这个回调
// 1. from 你从哪里来， 从哪来的路由信息对象
// 2. to   你往哪里去， 到哪去的路由信息对象
// 3. next() 是否放行，如果next()调用，就是放行 => 放你去想去的页面
//    next(路径) 拦截到某个路径页面
const whiteList = ['/login', '/register']
router.beforeEach((to, from, next) => {
  // to是从哪个路径过来,from是去哪个路径,next是调用才会进行下一步,不调用不进行下一步
  // console.log(to, from)
  const token = getToken()
  if (token) {
    // 如果有token就放行
    next()
  } else {
    // 如果没有token则判断在不在白名单中
    if (whiteList.includes(to.path)) {
      next()
    } else {
      // 不在则跳转到登陆页面
      next('/login')
    }
  }
})
export default router
