<template>
  <div class="like-page">
     <van-nav-bar title="面筋喜欢" fixed/>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!-- 引用封装好的插槽 -->
      <ArticleItem v-for="item in list" :key="item.id" :item="item">
        <!-- 使用具名插槽添加东西 -->
      </ArticleItem>

    </van-list>
  </div>
</template>

<script>
import { getArticleLike } from '@/api/article'

export default {
  name: 'like-page',
  data () {
    return {
      page: 1,
      loading: false,
      finished: false,
      list: []
    }
  },
  methods: {
    async  onLoad () {
      const { data } = await getArticleLike({
        page: this.page
      })
      this.list.push(...data.rows)
      this.page++
      this.loading = false
      if (this.page > data.pageTotal) this.finished = true
    }
  }
}
</script>

<style lang="less" scoped>
.like-page {
  margin-bottom: 50px;
  margin-top: 44px;
}
</style>
