<template>
  <div class="user-page">
    <div class="user">
      <img
        :src="avatar"
      />
      <h3>{{username}}</h3>
    </div>
    <van-grid column-num="3">
      <van-grid-item icon="clock-o" text="历史记录" to="/" />
      <van-grid-item icon="bookmark-o" text="我的收藏" to="/collect" />
      <van-grid-item icon="thumb-circle-o" text="我的点赞" to="/like" />
    </van-grid>
    <van-cell-group>
      <van-cell title="推荐分享" right-icon="arrow" is-link />
      <van-cell title="意见分享" right-icon="arrow" is-link />
      <van-cell title="关于我们" right-icon="arrow" is-link />
      <van-cell title="退出登陆" right-icon="arrow" is-link @click="clickFn"  />
    </van-cell-group>
  </div>
</template>

<script>
import { info, logout } from '@/api/user'
import { delToken } from '@/uitls/storage'
export default {
  name: 'user-page',
  data () {
    return {
      avatar: '',
      username: ''
    }
  },
  async created () {
    const { data } = await info()
    ;[this.avatar, this.username] = [data.avatar, data.username]
    // this.avatar = data.avatar
    // this.username = data.username
  },
  methods: {
    async clickFn () {
      this.$toast.success('请重新登陆')
      await logout()
      delToken()
      this.$router.push('/login')
    }
  }
}
</script>

<style lang="less" scoped>
.user-page {
  padding: 0 10px;
  background: #f5f5f5;
  height: 100vh;
  .mt20 {
    margin-top: 20px;
  }
  .user {
    display: flex;
    padding: 20px 0;
    align-items: center;
    img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
    }
    h3 {
      margin: 0;
      padding-left: 20px;
      font-size: 18px;
    }
  }
}
.van-cell-group{
  margin-top: 20px;
}
</style>
