<template>
  <div class="article-page">
    <nav class="my-nav van-hairline--bottom">
      <a
        href="javascript:;"
        @click="changeSorter('weight_desc')"
        :class="sorter === 'weight_desc' ? 'active' : ''"
        >推荐</a
      >
      <a
        href="javascript:;"
        @click="changeSorter(null)"
        :class="{ active: sorter === null }"
        >最新</a
      >
      <div class="logo">
        <img src="@/assets/logo.jpg" alt="" />
      </div>
    </nav>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!-- 引用封装好的插槽 -->
      <ArticleItem v-for="item in list" :key="item.id" :item="item" >
        <!-- 使用具名插槽添加东西 -->
      </ArticleItem>

    </van-list>
  </div>
</template>

<script>
import ArticleItem from '@/components/ArticleItem.vue'
import { getArticleS } from '@/api/article'
export default {
  name: 'article-page',
  data () {
    return {
      finished: false,
      loading: false,
      list: [],
      current: 1, // 设置加载初始值,
      sorter: 'weight_desc'
    }
  },
  methods: {
    async onLoad () {
      const { data } = await getArticleS({
        current: this.current,
        sorter: this.sorter
      })
      // console.log(rows)
      // console.log(this.loading)
      this.list.push(...data.rows) // 将第一次以后的数据push进数据列表里
      this.current++ // 加载数据累加
      this.loading = false
      // setTimeout(() => {
      //   // 加载状态改为false
      // }, 2000)
      // 如果current大于服务器内的数据则停止请求
      if (this.current > data.pageTotal) this.finished = true
    },
    changeSorter (type) {
      this.list = []
      this.sorter = type
      this.finished = false
      this.loading = true
      // 避免重新加载,所有把loading设置为true
      this.current = 1
      this.onLoad()
    }
  },
  components: {
    ArticleItem
  },
  async created () {
    // const { data: { rows } } = await getArticleS({
    //   current: 10
    // })
    // console.log(rows)
    // this.list = rows
  }
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: "";
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
  .head {
    display: flex;
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
    }
    .con {
      flex: 1;
      overflow: hidden;
      padding-left: 10px;
      p {
        margin: 0;
        line-height: 1.5;
        &.title {
          width: 280px;
        }
        &.other {
          font-size: 10px;
          color: #999;
        }
      }
    }
  }
  .body {
    font-size: 14px;
    color: #666;
    line-height: 1.6;
    margin-top: 10px;
  }
  .foot {
    font-size: 12px;
    color: #999;
    margin-top: 10px;
  }
}
</style>
