<template>
  <div class="article-detail-page">
    <nav class="nav">
      <van-nav-bar@click-left="$router.go(-1)"
        title="面筋详情"
        left-text="返回"
        fixed
      />
    </nav>
    <header class="header">
      <h1>{{ message.stem }}</h1>
      <p>
        {{ message.createdAt }} | 浏览量 {{ message.views }} | 点赞数
        {{ message.likeCount }}
      </p>
      <p>
        <img :src="message.avatar" />
        <span>{{ message.creator }}</span>
      </p>
    </header>
    <main class="body" v-html="message.content"></main>
    <div class="opt">
      <van-icon
        :name="message.likeFlag ? 'like' : 'like-o'"
        color="rgb(255,113,0)"
        @click="toogleLike"
      />
      <van-icon
        :name="message.collectFlag ? 'star' : 'star-o'"
        color="rgb(255,113,0)"
        @click="toogleCollect"
      />
    </div>
  </div>
</template>

<script>
import { getArticleDetail, toogleLike } from '@/api/article'

export default {
  name: 'detail-page',
  data () {
    return {
      message: () => ({}),
      like: false,
      star: false
    }
  },
  async created () {
    const { data } = await getArticleDetail(this.$route.params.id)
    // console.log(data)
    this.message = data
  },
  methods: {
    // async likeClick () {
    //   this.like = !this.like
    //   const id = this.$route.params.id
    //   // console.log(id)
    //   const res = await toogleLike(id)
    //   console.log(res)
    // },
    // starClick () {
    //   this.star = !this.star
    // }
    // 点赞
    async toogleLike () {
      await toogleLike(this.message.id) // 发送请求
      this.message.likeFlag = !this.message.likeFlag // 状态取反
      if (this.message.likeFlag) {
        this.$toast.success('点赞成功')
        this.message.likeCount++
      } else {
        this.$toast.success('取消点赞')
        this.message.likeCount--
      }
      console.log(this.message.likeFlag)
      // console.log(await toogleLike(this.message.id))
      // const { data } = await getArticleDetail(this.$route.params.id)
      // console.log(data)
    },
    // 收藏
    async toogleCollect () {
      await toogleLike(this.message.id) // 发送请求
      this.message.collectFlag = !this.message.collectFlag // 状态取反
      if (this.message.collectFlag) {
        this.$toast.success('收藏成功')
        // this.message.likeCount++
      } else {
        this.$toast.success('取消成功')
        // this.message.likeCount--
      }
      console.log(this.message.likeFlag)
      // console.log(await toogleLike(this.message.id))
      // const { data } = await getArticleDetail(this.$route.params.id)
      // console.log(data)
    }
  }
}
</script>

<style lang="less" scoped>
.detail-page {
  margin-top: 44px;
  overflow: hidden;
  padding: 0 15px;
  .header {
    h1 {
      font-size: 24px;
    }
    p {
      color: #999;
      font-size: 12px;
      display: flex;
      align-items: center;
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
    }
  }
}
.article-detail-page {
  .nav {
    height: 44px;
    border-bottom: 1px solid #e4e4e4;
    line-height: 44px;
    text-align: center;
    .back {
      font-size: 18px;
      color: #666;
      position: absolute;
      left: 10px;
      top: 0;
      transform: scale(1, 1.5);
    }
  }
  .header {
    padding: 0 15px;
    p {
      color: #999;
      font-size: 12px;
      display: flex;
      align-items: center;
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
    }
  }
  .body {
    padding: 0 15px;
  }
}
.opt {
  position: fixed;
  bottom: 100px;
  right: 0;
  > .van-icon {
    margin-right: 20px;
    background: #fff;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    box-shadow: 2px 2px 10px #ccc;
    font-size: 18px;
    &.active {
      background: #fec635;
      color: #fff;
    }
  }
}
</style>
