<template>
  <div class="collect-page">
     <van-nav-bar title="面筋收藏" fixed />
   <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!-- 引用封装好的插槽 -->
      <ArticleItem v-for="item in list" :key="item.id" :item="item">
        <!-- 使用具名插槽添加东西 -->
      </ArticleItem>
    </van-list>
  </div>
</template>

<script>
import { getArticleCollect } from '@/api/article'
import ArticleItem from '@/components/ArticleItem.vue'
export default {
  name: 'collect-page',
  data () {
    return {
      loading: false,
      finished: false,
      page: 1,
      list: []
    }
  },
  components: {
    ArticleItem
  },
  methods: {
    async onLoad () {
      const { data } = await getArticleCollect({ page: this.page })
      this.list.push(...data.rows)
      // console.log(...data.rows)
      // console.log(data)
      this.loading = false
      this.page++
      if (this.page > data.pageTotal) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.collect-page {
  margin-bottom: 50px;
  margin-top: 44px;
}
</style>
